.result {
  overflow-x: auto;
  max-width: 100%;
  padding: 1rem;
  border-spacing: 1rem;
  text-align: center;
}
.result thead {
  font-size: 0.7rem;
  color: #ad9d09;
}
.result tbody {
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 0.85rem;
  color: #b6b6b6;
}

.table-container{
  max-width: 100%;
  overflow-x: auto;
}
table{
  width: 100%;
  border-collapse: collapse;
}
