.form {
  padding: 3rem;
  max-height: 30rem;
  max-width: 60rem;
  margin: 1rem auto;
  border-radius: 20px;
  background: linear-gradient(180deg, #9fa116, #c0af16);
}

.input-group {
  display: flex;
  justify-content: space-evenly;
  gap: 1.5rem;
  color: #171818;

 
}

.form label {
  display: block;
  
  margin-bottom: 0.25rem;
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 0.7rem;
  font-weight: bold;
  text-transform: uppercase;
}

.form input {
  width: 100%;
  padding: 0.5rem;
  border: 1px solid #3d3d3d;
  border-radius: 20px;
  background-color: transparent;
  color: #171818;
  font-size: 1rem;
}

.actions {
  display: flex;
  justify-content: center;
  gap: 1rem;
}

.button {
  padding: 1.1rem 1.1rem;
  border: none;
  border-radius: 16px;
  background: #333333;
  color: #b5b81a;
  font-family: 'Roboto Condensed', sans-serif;
  cursor: pointer;
}

.buttonAlt {
  font-family: 'Roboto Condensed', sans-serif;
  border: none;
  background: transparent;
  color: #a0a0a0;
  cursor: pointer;
}

.button:hover {
  background: #b9b9b9;
}

.buttonAlt:hover {
  background: transparent;
  color: #252525;
}
